import React from 'react';
import Head from 'next/head';
import fs from 'fs';
import path from 'path';
import { Zap, Shield, Globe, Layout, ChevronRight } from 'lucide-react';

export default function LandingPage({ content }) {
  
  // Helper to render icons based on JSON string
  const getIcon = (iconName) => {
    if (iconName === 'Zap') return <Zap className="text-yellow-400" />;
    if (iconName === 'Shield') return <Shield className="text-pink-400" />;
    return <Globe className="text-blue-400" />;
  };

  return (
    <>
      <Head>
        <title>{content.brand.name} | Welcome</title>
      </Head>

      <div className="min-h-screen bg-[#0B0B0F] text-white overflow-x-hidden flex flex-col font-sans selection:bg-purple-500/30">
        
        {/* 1. Navbar */}
        <nav className="max-w-7xl mx-auto px-6 py-6 flex justify-between items-center w-full z-20">
          <div className="flex items-center gap-2 font-bold text-xl tracking-tight">
            <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-pink-500 to-purple-600 flex items-center justify-center">
              <Layout size={18} className="text-white" />
            </div>
            <span>
              {content.brand.name}
              <span className="text-purple-400">{content.brand.highlight}</span>
            </span>
          </div>
          {/* Simple Login Link Placeholder */}
          <a href="/login" className="text-sm text-gray-400 hover:text-white transition-colors">
            Login
          </a>
        </nav>

        {/* 2. Hero Section */}
        <main className="flex-grow flex flex-col items-center justify-center text-center px-4 relative z-10 mt-10 md:mt-20">
          
          {/* Background Glow Effects */}
          <div className="absolute top-[-100px] left-1/2 -translate-x-1/2 w-[600px] h-[600px] bg-purple-600/20 rounded-full blur-[120px] pointer-events-none" />

          {/* Version Badge */}
          <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-white/5 border border-white/10 text-xs font-medium text-purple-300 mb-8">
            <span className="w-2 h-2 rounded-full bg-green-500 animate-pulse"></span>
            {content.brand.version}
          </div>

          {/* Main Title */}
          <h1 className="text-5xl md:text-7xl font-bold tracking-tight mb-6 max-w-4xl leading-tight">
            {content.hero.titleStart} <br />
            <span className="bg-clip-text text-transparent bg-gradient-to-r from-pink-500 via-purple-500 to-blue-500">
              {content.hero.titleEnd}
            </span>
          </h1>

          {/* Description */}
          <p className="text-lg text-gray-400 max-w-2xl mb-10 leading-relaxed">
            {content.hero.description}
          </p>

          {/* Buttons */}
          <div className="flex flex-col sm:flex-row gap-4">
            <button 
              onClick={() => console.log("Open Login Modal Here")} 
              className="px-8 py-4 bg-gradient-to-r from-pink-600 to-purple-600 hover:opacity-90 rounded-full font-bold text-white shadow-lg shadow-purple-500/20 transition-all transform hover:-translate-y-1 flex items-center justify-center gap-2"
            >
              {content.hero.button} <ChevronRight size={18} />
            </button>
            <button className="px-8 py-4 bg-white/5 border border-white/10 hover:bg-white/10 rounded-full font-bold text-white transition-all">
              {content.hero.secondaryButton}
            </button>
          </div>

          {/* Feature Grid */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-24 max-w-6xl w-full text-left pb-20">
            {content.features.map((feature, index) => (
              <div key={index} className="p-6 rounded-2xl bg-[#121212] border border-white/10 hover:border-purple-500/30 transition-colors group">
                <div className="w-12 h-12 rounded-lg bg-white/5 flex items-center justify-center mb-4 group-hover:scale-110 transition-transform">
                  {getIcon(feature.icon)}
                </div>
                <h3 className="text-xl font-bold mb-2 text-white">{feature.title}</h3>
                <p className="text-gray-400 text-sm leading-relaxed">{feature.desc}</p>
              </div>
            ))}
          </div>
        </main>

        {/* 3. Footer */}
        <footer className="border-t border-white/5 bg-[#0B0B0F] relative z-20">
          <div className="max-w-7xl mx-auto px-6 py-8 flex flex-col md:flex-row justify-between items-center gap-4">
            <p className="text-gray-500 text-sm">{content.footer.text}</p>
            <div className="flex gap-6">
              {content.footer.links.map((link, i) => (
                <span key={i} className="text-gray-500 hover:text-white text-sm cursor-pointer transition-colors">
                  {link}
                </span>
              ))}
            </div>
          </div>
        </footer>

      </div>
    </>
  );
}

// This function runs on the SERVER (or build time). 
// It reads the file so the data is ready immediately when the page loads.
export async function getStaticProps() {
  const filePath = path.join(process.cwd(), 'public', 'site-content.json');
  const jsonData = fs.readFileSync(filePath, 'utf8');
  const content = JSON.parse(jsonData);

  return {
    props: {
      content,
    },
  };
}